.banner{

    background-size: cover;
    height: 405px;
    color: white;
    
}

.content{
    padding-top: 140px;
    height: 100px;
    padding-left:15px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    

}
.button{
    color:white;
    outline: none;
    border:none;
    font-weight: 700;
    border-radius:5px;
    padding-left: 2rem;
    padding-right:2rem;
    padding-top:0.5rem;
    padding-bottom:0.5rem;
    background-color: rgba(51,51,51,.5);
    margin-right: 1rem;
    margin-left: 0.5rem;
}

.button:hover{
    background-color: rgb(192, 213, 231);
}

.description{
    width:45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height:80px;
    max-width:360px;
    margin-left: 0.5rem;
    max-height: 150px;
}

.fade{
  height:10.5rem;
  background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}